* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background-color: black;
  color: white;
  font-family: "Poppins", sans-serif;
}
.navbar {
  position: absolute;
  top: 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 3% 3%;
  z-index: 10;
}
.navbar__brand {
  width: 100px;
  height: 100%;
}
.brand__logo {
  width: 100%;
  height: 100%;
}
.language__drop__down {
  background: transparent;
  border: none;
  color: white;
}
.language__drop__down:focus {
  outline: none;
}
.dropdown__container {
  background: rgba(0, 0, 0, 0.4);
  border: 1px solid white;
  padding: 0.4rem;
  border-radius: 4px;
}
.signin__button {
  background-color: #dc030f;
  border: 1px solid #dc030f;
  color: white;
  padding: 0.25rem 0.5rem;
  border-radius: 4px;
}
.navbar__nav__items {
  display: flex;
  gap: 10px;
}
.hero__bgimage__container {
  width: 100%;
  height: 80vh;
}
.hero__bg__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.hero__bg__overlay {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 80vh;
  background: rgba(0, 0, 0, 0.4);
  background-image: linear-gradient(
    to top,
    rgba(0, 0, 0, 0.8) 0,
    rgba(0, 0, 0, 0) 60%,
    rgba(0, 0, 0, 0.8) 100%
  );
}
.hero__card {
  position: absolute;
  top: 20%;
  text-align: center;
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.hero__title {
  font-weight: 600;
  font-size: 2rem;
}
.hero__subtitle,
.hero__description {
  font-weight: 400;
  font-size: 1rem;
}
.hero__description {
  margin: 0 50px;
}
.email__input {
  border: none;
  background-color: transparent;
  width: 100%;
  height: 100%;
  padding-left: 7px;
}
.email__label {
  color: black;
  position: absolute;
  top: 32%;
  left: 2%;
  color: rgba(153, 149, 149);
  transition: 0.5s;
}
.email__form__container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 15px;
}
.form__container {
  background-color: white;
  width: 90%;
  height: 50px;
  position: relative;
}
.email__input:focus + .email__label {
  top: 0.2rem;
  font-size: 10px;
}
.email__input:not(:placeholder-shown).email__input:not(:focus) + .email__label {
  top: 0.2rem;
  font-size: 10px;
}
.email__input:focus-visible {
  outline: none;
}
.primary__button {
  background-color: #dc030f;
  border: 1px solid #dc030f;
  color: white;
  padding: 0.8rem 1.2rem;
  border-radius: 2px;
  font-size: 15px;
  letter-spacing: 1px;
}
.feature {
  border-top: 8px solid #222;
  padding: 2rem 1rem;
}
.feature__title {
  font-weight: 600;
  font-size: 25px;
  margin: 10px 0;
}
.feature__sub__title {
  font-weight: 400;
  font-size: 15px;
}
.feature__details {
  text-align: center;
}
.feature__image__container {
  width: 100%;
  position: relative;
}
.feature__image {
  width: 100%;
}
.feature__backgroundvideo__container {
  position: absolute;
  width: 100%;
  top: 20%;
  left: 12%;
  height: 100%;
  max-width: 73%;
  max-height: 54%;
  z-index: 2;
}
.feature__background__video {
  width: 100%;
}
.feature__2__poster__container {
  display: flex;
  align-items: center;
  background-color: black;
  border: 2px solid rgba(255, 255, 255, 0.25);
  border-radius: 10px;
}
.poster__container {
  width: 30%;
}
.poster {
  width: 100%;
}
.poster__details {
  width: 50%;
}
.download__gif__container {
  width: 20%;
}
.gif {
  width: 100%;
}
.faq__accordian {
  width: 100%;
}
.faq__title {
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 15px 20px;
  font-size: 18px;
  background-color: #303030;
  border: 1px solid #303030;
  color: white;
}
.faq__visible {
  background-color: #303030;
  border-top: 1px solid black;
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.5s ease-in-out;
}
.faq__visible > p {
  margin: 1rem;
}
.faq__list__container {
  border-top: 8px solid #222;
  padding: 2rem 0;
}
.faq__heading {
  font-size: 20px;
  text-align: center;
  font-weight: 600;
  margin: 1rem 3rem;
}
.faq__list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
}
.faq__get__started__email {
  text-align: center;
}
.faq__get__started__email > h3 {
  font-size: 18px;
  font-weight: 400;
  margin: 2rem 0;
}
footer {
  border-top: 8px solid #333;
  padding: 2rem 1rem;
  color: #757575;
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.footer__row__2 {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: space-around;
  gap: 20px;
}
.footer__row__1 > h4 {
  font-size: 15px;
  font-weight: 500;
}
.footer__row__3 > .dropdown__container {
  width: fit-content;
  border: 1px solid #757575;
}
.footer__row__3 > .dropdown__container > .language__drop__down {
  color: #757575;
}

@media (min-width: 768px) {
  .hero__bgimage__container {
    height: 100vh;
  }
  .hero__bg__overlay {
    height: 100vh;
  }
  .hero__card {
    top: 40%;
    left: 30%;
    transform: translate(-20%, -20%);
  }
  .hero__title {
    font-size: 3rem;
  }
  .hero__subtitle,
  .hero__description {
    font-size: 1.3rem;
  }
  .feature {
    padding: 4rem;
  }
  .feature__title {
    font-size: 35px;
  }
  .feature__sub__title {
    font-size: 20px;
  }
  .faq__list {
    padding: 1rem 5rem;
  }
  .faq__title {
    font-size: 20px;
  }
  .faq__heading {
    font-size: 38px;
    font-weight: 500;
    letter-spacing: 1px;
  }
  .faq__get__started__email {
    width: 75%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    transform: translate(15%);
  }
  .footer__row__2 {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    gap: 20px;
  }
}
@media (min-width: 1024px) {
  .navbar {
    padding: 2% 5%;
  }
  .navbar__brand {
    width: 150px;
  }
  .signin__button {
    padding: 10px 20px;
    font-size: 18px;
  }
  .dropdown__container {
    padding: 10px 5px;
  }
  .navbar__nav__items {
    gap: 30px;
  }
  .hero__card {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  .email__form__container {
    flex-direction: row;
    align-items: center;
    gap: 1px;
  }
  .form__container {
    width: 50%;
  }
  .primary__button {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 50px;
  }
  .feature {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
  }
  .feature__details {
    text-align: left;
    padding: 0 6rem;
  }
  .feature__title {
    font-size: 50px;
  }
  .feature__sub__title {
    font-size: 25px;
  }
  .feature__image__container {
    max-width: 500px;
  }
  .feature:nth-child(even) {
    flex-direction: row-reverse;
  }
  .faq__get__started__email {
    transform: translate(16%);
  }
  footer {
    padding: 2rem 12rem;
    justify-content: flex-start;
    text-align: left;
    align-items: flex-start;
  }
  .footer__row__1,
  .footer__row__3,
  .footer__row__4 {
    align-self: flex-start;
  }
  .footer__row__2 {
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: nowrap;
  }
}

@media (min-width: 1254px) {
}
